.elements__list {
  margin: 0 auto;
  padding: 0;
  list-style: none;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(282px, 1fr));
  column-gap: 17px;
  row-gap: 20px;
}

@media (max-width: 910px) {
  .elements__list {
    justify-items: center;
  }
}